<template>
    <shade-slots>
        <template #card @click.stop>
            <div id="register-wrapper" @click.stop>

                <div id="main-title" :style="main_title[0].style" class="block">
                    <div class="title">{{ main_title[main_title[0].step].title }}</div>
                    <div class="describe">{{ main_title[main_title[0].step].describe }}</div>
                </div>

                <div id="inputLine" class="block">
                    <div id="long-line" :style="inputStyle">
                        <div class="inputBox">
                            <label>
                                <input type="text" placeholder="请输入邮箱">
                            </label>
                        </div>
                        <div class="inputBox">
                            <label>
                                <input type="text" placeholder="请输入验证码">
                            </label>
                        </div>
                        <div class="inputBox">
                            <label>
                                <input type="text" placeholder="昵称">
                            </label>
                        </div>

                    </div>
                </div>

                <div id="btnLine" class="block">
                    <button class="icon iconfont icon-direction-right pointer" @click="stepController(true)"></button>
                    <div class="back">
                        <button class="icon iconfont icon-back back-btn pointer"
                                @click="stepController(false)"></button>
                    </div>
                </div>


            </div>
        </template>
    </shade-slots>
</template>

<script>
import ShadeSlots from './ShadeSlots'

export default {
    name: 'Register',
    data() {
        return {
            main_title: [
                {
                    step: 1,
                    style: {
                        opacity: '1',
                        transition: 'opacity .1s',
                    },
                },
                {
                    title: '输入邮箱',
                    describe: '输入正确的邮箱并接受验证码以完成验证',
                },
                {
                    title: '输入验证码',
                    describe: '输入正确以继续注册'
                },
                {
                    title: '用户名',
                    describe: '输入自己的用户名'
                },
            ],
            step: 1,
        }
    },
    components: {ShadeSlots},
    mounted() {

    },
    computed: {
        inputStyle() {
            return {
                transition: 'all .2s',
                opacity: this.step,
                transform: 'translateX(-' + (this.step - 1) * 375 + 'px)'
            }
        }
    },
    methods: {
        stepController(isNext) {

            let changeTitle = (isAdd) => {
                let timer = 100
                this.main_title[0].style.opacity = '0'
                setTimeout(() => {
                    isAdd ? this.main_title[0].step++ : this.main_title[0].step--
                    this.main_title[0].style.opacity = '1'
                }, timer)
            }
            /* 下一步 */
            if (isNext && this.step < 3) {
                changeTitle(true)
                this.step++

            } else if (!isNext && this.step > 1) {
                changeTitle(false)
                this.step--

            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/assets/css/style';
@import 'src/assets/css/base';

#register-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .block {
        height: 150px;
        box-sizing: border-box;
        @include flex;
        flex-direction: column;
        /*border: 1px solid black;*/
    }

    #main-title {
        .title {
            font-size: 30px;
            font-weight: bolder;
        }

        .describe {
            margin: 10px;
            color: rgb(142, 142, 146);
        }
    }

    #inputLine {
        display: flex;
        align-items: unset;
        justify-content: flex-start;
        overflow: hidden;

        #long-line {
            width: 300vw;
            height: 150px;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            .inputBox {
                width: 100vw;
                height: 150px;
                @include flex;

                input {
                    width: 300px;
                    box-sizing: border-box;
                    height: 40px;
                    border-radius: 5px;
                    background-color: rgb(238, 238, 239);
                    font-size: 16px;
                    padding: 0 10px 0 10px;
                }
            }
        }

    }


    #btnLine {
        position: relative;

        button {
            width: 100px;
            height: 100px;
            color: black;
            border-radius: 100px;
            font-size: 50px;
            background-color: rgb(75, 181, 220);
            transition: all .2s;

            .active {
                transform: scale(.95);
            }

        }

        .back {
            @include flex;
            width: 100px;
            height: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
        }

        .back-btn {
            background-color: rgb(238, 238, 239);
            width: 100px;
            margin-left: 30px;
            height: 50px;
            font-size: 30px;
            border-radius: 10px;
        }
    }

}
</style>